import { KeyValue } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';
import { UtilsService } from 'src/app/services/utils.service';
import { CompanyService } from 'src/app/services/company.service';

interface Filters {
  nit: string | null,
  code: string | null,
  status: string | null
}

@Component({
  selector: 'app-admin-empresas',
  templateUrl: './admin-empresas.component.html',
  styleUrls: ['./admin-empresas.component.css']
})
export class AdminEmpresasComponent implements OnInit {
  public pageSize = 20;
  public pageNumber = 0;
  public totalPages = 0;
  public loading: boolean = false;
  public filter: string | null = '';
  public role: string | undefined = undefined;
  public env: boolean = environment.production;

  public categoriesHeading: string[] = [];
  public list: {}[] = [{ 'Nombre de la Empresa': '', 'Código': '', 'NIT': '', 'Teléfono': '', 'Correo Empresa': '', 'Estatus Socio': '' }];

  constructor(
    private utilsService: UtilsService,
    private companyService: CompanyService
  ) { this.categoriesHeading = Object.keys(this.list[0]); }

  ngOnInit() {
    this.role = sessionStorage.getItem('Role')?.toString();

    this.isShowDiv = true;
    if (sessionStorage.getItem('Token')) {
      this.accessToken = sessionStorage.getItem('Token');
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', 'Inicia sesión con un usuario válido.');
      this.goToPage('/login');
    }
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  editUser(event: any, index: number) {
    const companyID = this.companyList[index].id;
    this.utilsService.goToPage('/admin/users/edit-empresas', { ID: companyID });
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    var element = document.getElementById('item' + index);
    var listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + index) !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  async searchReturn() {
    if (this.filter!.length === 0) {
      this.filter = null;
      this.totalPages = 0;
      this.pageNumber = 0;
      await this.loadData(this.pageNumber, this.pageSize, null, false);
    }
  }

  loadData(page: number | null, size: number | null, filter: string | null, search?: boolean) {
    this.loading = true;

    if (search) {
      page = 0;
      this.pageNumber = 0;
    }

    this.companyService.getCompanies(this.pageNumber, this.pageSize, this.filter).then(
      res => {
        this.companyList = res.result.rows;
        this.list = [];
        console.log(res.result.rows);
        if (res.success && res.result.rows) {
          this.list = [];
          this.list = res.result.rows.map((e: any) => {
            return {
              id: e?.id,
              'Nombre de la Empresa': e?.nombreComercial || '',
              "Código": e?.codigoCRM,
              "NIT": e?.nit,
              "Teléfono": e?.telefonoGeneral,
              "Correo Empresa": e?.emailEmpresarial,
              "Estatus": e?.socio == true ? 'Activo' : 'No Activo'
            }
          });
          this.totalPages = Math.ceil(res.count / this.pageSize);
        } else {
          this.list = [];
          this.utilsService.dialog('ERROR', 'Error', res?.error?.message?.description || '¡Error al consultar empresas!');
        }
      },
      error => {
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
      }
    ).finally(() => {
      this.loading = false;
    })
  }

  onLeft() {
    if (this.pageNumber + 1 != 1) {
      this.pageNumber--;
      this.list.length = 0;
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
  }

  onFirst() {
    if (this.pageNumber != 0) {
      this.pageNumber = 0;
      this.list.length = 0;
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
  }

  onRight() {
    if (this.pageNumber + 1 != this.totalPages) {
      this.pageNumber++;
      this.list.length = 0;
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
  }

  onLast() {
    if (this.pageNumber != this.totalPages) {
      this.pageNumber = (this.totalPages - 1);
      this.list.length = 0;
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
  }

  //TS para el modal hijo
  accessToken: any;
  companyList: any;
  isShowDiv: boolean = true;
  statusDesc = "De baja";

  // public filters: {
  //   nit: string | null,
  //   code: string | null,
  //   status: string | null
  // } = { nit: null, code: null, status: 'active' }
  // public filters: Filters = {} as Filters;
  public isFilter: boolean = false;
  public showFilterModal: boolean = false;
  public filters: Filters = { nit: null, code: null, status: 'active' };


  searchFilter() {
    this.isFilter = true;
    this.filter = this.filters.code || this.filters.nit || this.filters.status;
    this.loadData(this.pageNumber, this.pageSize, this.filter);
  }

  clearFilter(type: string) {
    switch (type) {
      case 'code':
        this.filters.code = null;
        this.loadData(this.pageNumber, this.pageSize, this.filter);
        break;
      case 'nit':
        this.filters.nit = null;
        this.loadData(this.pageNumber, this.pageSize, this.filter);
        break;
      case 'all':
        this.filter = null;
        this.isFilter = false;
        this.filters.nit = null;
        this.filters.code = null;
        this.filters.status = 'active';
        this.loadData(this.pageNumber, this.pageSize, this.filter);
        break;
    }
  }
}